import React from "react";
import styled from "styled-components";

export const GreetingsHeader = () => {
  return (
    <Header>
      <h1 className="greeting__header">Liquid-Mix</h1>
      <h3>     </h3>
      <p className="greeting_p">
              .   
          .    ,  
            .  
          ,     , 
         92  .
      </p>
    </Header>
  );
};

const Header = styled.header`
  grid-area: greeting;
  margin: 0 1rem;
  text-align: center;
  color: white;
  h1 {
    margin: 0.5rem;
    margin-bottom: 0;
  }
  h3 {
    margin: 0.15rem;
    line-height: 0.95;
  }
  p {
    font-size: 0.8rem;
  }
  @media (min-width: 992px) {
    h1 {
      margin: 0.625rem;
    }
    h3 {
      margin: initial;
      line-height: 1;
    }
    p {
      font-size: 1rem;
    }
  }
`;
